<template>
	<!-- https://ext.dcloud.net.cn/plugin?id=5459#p1 -->
	<view class="box-compents">
		
		<lsj-upload ref="lsjUpload" childId="upload1" :width="width" :height="height" :option="option" :size="size"
			:formats="formats" :debug="true" :instantly="false" @progress="onProgress" @change="onChange" @uploadEnd="onuploadEnd">
			<view class="upload-btn">选择附件</view>
		</lsj-upload>
		
		<view style="margin-bottom:10px;" v-show="fileShow">
			<view>已选择文件列表：</view>
			<view class="my-file-list" v-for="(item,index) in files.values()" :key="index">
				<image :src="item.path" mode="widthFix"></image>
				<view class="my-file-list-r">
					<!-- <text>{{item.path}}</text> -->
					<view class="cu">{{item.name}}</view>
					<view style="display:flex;justify-content:space-around;">
						<text>{{item.size}}</text>
						<!-- <text v-else-if="item.size/1000<1000">{{item.size/1000}}KB</text>
						<text v-else-if="item.size/1000/1000<1000">{{item.size/1000/1000}}M</text> -->
						<view style="display:flex;align-items:center;" v-if="item.type=='success'">
							<view class="small-circle" style="background-color:#5AC725;"></view>
							<text>成功</text>
						</view>
						<view style="display:flex;align-items:center;" v-else-if="item.type=='waiting'">
							<view class="small-circle" style="background-color:#F9AE3D;"></view>
							<text>待上传</text>
						</view>
						<view style="display:flex;align-items:center;" v-else-if="item.type=='fail'">
							<view class="small-circle" style="background-color:#F56C6C;"></view>
							<text>失败</text>
						</view>
					</view>
					<view class="del-btn" @click="clear(item.name)">删除</view>
					
					<!-- <text style="margin-left: 10rpx;">进度：{{item.progress}}</text> -->
					<!-- <text style="margin-left: 10rpx;" v-if="item.responseText">服务端返回演示：{{item.responseText.code}}</text> -->
					
				</view>
			</view>
		</view>
		
		<u-button type="primary" throttleTime="1000" class="my-primary-button sticky-bottom" text="上传" v-show="fileShow" style="margin-top:10px;" @tap="sdUpload">
		</u-button>
		<u-toast ref="uToast"></u-toast>
		

	</view>
</template>

<script>
	export default {
		data() {
			return {
				option: {
					// url: "http://self.mzsale.com/mzsale/web/basic/upload",
					url: "http://erpai.mzsale.com/mzsale/web/basic/upload",
					// 上传附件的key
					name: "file",
					header: {
						
					},
					formData: {
						
					}
				},
				width: "70vw",
				height: "40px",
				// 限制允许选择的格式，空串=不限制，默认为空
				// formats: "",
				formats: "txt,jpg,png,doc,pdf,jpeg,xls,xlsx,docx,BMP,ppt,pptx",
				// 文件上传大小限制
				size: 10,
				// 文件回显列表
				files: new Map(),
				fileShow:false,
				fileArr:[]
			}
		},
		mounted() {
			
		},
		methods: {
			// 某文件上传结束回调(成功失败都回调)
			onuploadEnd(item) {
				console.log("上传结束回调 item",item);
				this.fileArr.push({
					"data":item.responseText
				})
				this.files.set(item.name, item);
				if (item["responseText"]) {
					this.files.get(item.name).responseText = JSON.parse(item.responseText);
				}
				this.$forceUpdate();

				// ---可删除--演示判断是否所有文件均已上传成功
				let isAll = [...this.files.values()].find(item => item.type !== "success");
				if (!isAll) {
					uni.hideLoading()
					this.$refs.uToast.show({
						type: "success",
						message: "上传成功"
					})
					setTimeout(()=>{
						this.$emit("scFileData",this.fileArr)
					},1200)
				} else {
					console.log("待上传:",isAll.name);
				}

			},
			// 上传进度回调
			onProgress(item) {
				// 更新当前状态变化的文件
				this.files.set(item.name, item);
				console.log("打印对象", JSON.stringify(this.files.get(item.name)));
				this.$forceUpdate();
			},
			// 文件选择回调
			onChange(files) {
				this.files = files;
				this.fileShow=true
				this.$forceUpdate();
				console.log("文件选择回调...",...this.files)
			},
			// 手动上传
			sdUpload() {
				// name=指定文件名，不指定则上传所有type等于waiting和fail的文件
				uni.showLoading({
					title: "上传中，请稍后"
				});
				this.$refs.lsjUpload.upload();
			},
			// 移除某个文件
			clear(name) {
				// name=指定文件名，不传name默认移除所有文件
				this.$refs.lsjUpload.clear(name);
			},

		},
		
	}
</script>

<style lang="scss" scoped>
	.box-compents{
		padding-top: 10px;
		/deep/.lsj-file{
			margin-left: 15vw;
		}
		
		.upload-btn{
			width: 100%;
			height: 40px;
			line-height: 40px;
			text-align: center;
			background-image: linear-gradient(to right, var(--nav-left-color), var(--nav-center-color), var(--nav-right-color));
			border-radius: 20px;
			color: #fff;
		}
		
		.my-file-list{
			width: 100%;
			display: flex;
			justify-content: space-between;
			box-sizing: border-box;
			border-bottom: 1px solid #E6E6E6;
			uni-image{
				width: 90px;
				max-height: 90px;
				margin-top: auto;
				margin-bottom: auto;
			}
			.my-file-list-r{
				width: calc(100% - 98px);
				.cu{
					font-weight: 600;
					color: #2c2c2c;
					word-break: break-all;
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
				.small-circle{
					width:6px;
					height:6px;
					border-radius: 6px;
					margin-right: 5px;
				}
				.del-btn{
					width: 50px;
					padding:2px 0px;
					border-radius: 10px;
					background-color: #F56C6C;
					color: #fff;
					text-align: center;
				}
			}
			.my-file-list-r>*{
				margin-bottom: 8px;
			}
			
			
		}
		
	}
</style>